import {HttpEventType} from '@angular/common/http';
import {throwError} from 'rxjs';
import {UiSnackbar} from 'ng-smn-ui';

export class ApiReponse {

    static extractData(res, callback, cleanResult) {
        if (!callback) {
            return;
        }

        if (cleanResult) {
            callback(res);
        }

        if (res.type === HttpEventType.Response && !cleanResult) {
            callback(res.body);
        }
    }

    static handleError(res, callback, cleanError) {
        if (callback) {
            callback(res);
        }

        if (cleanError) {
            return throwError(res);
        }

        switch (res.status) {
            case 0:
                UiSnackbar.show({
                    text: `Um de nossos servios est fora do ar e no foi possvel processar sua requisio. Tente novamente mais tarde.`,
                    actionText: 'OK',
                    action: close => close(),
                    duration: 1000
                });
                break;
            case 400:
                UiSnackbar.show({
                    text: `Requisio invlida. Verifique as informaes fornecidas.`,
                    actionText: 'OK',
                    action: close => close()
                });
                break;
            case 409:
                UiSnackbar.show({
                    text: res.error.message,
                    actionText: 'OK',
                    action: close => close()
                });
                break;
            case 500:
                UiSnackbar.show({
                    text: `Ocorreu um erro interno. J fomos informados do erro. Tente novamente mais tarde.`,
                    actionText: 'OK',
                    action: close => close(),
                    duration: 1000
                });
        }

        return throwError(res);
    }

}
